"use client"

import { useEffect, useRef, useState } from 'react'

const PADDLE_WIDTH = 60
const PADDLE_HEIGHT = 10
const BALL_SIZE = 6
const BRICK_WIDTH = 40
const BRICK_HEIGHT = 15
const BRICK_GAP = 4
const GAME_WIDTH = 400
const GAME_HEIGHT = 500

type Brick = {
  x: number
  y: number
  visible: boolean
}

export default function Arkanoid() {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const [gameStarted, setGameStarted] = useState(false)
  const [score, setScore] = useState(0)
  const requestRef = useRef<number>()
  const ballRef = useRef({
    x: GAME_WIDTH / 2,
    y: GAME_HEIGHT - 30,
    dx: 3,
    dy: -3
  })
  const paddleRef = useRef({
    x: GAME_WIDTH / 2 - PADDLE_WIDTH / 2
  })
  const [bricks, setBricks] = useState<Brick[]>(() => {
    const bricks: Brick[] = []
    const ROWS = 5
    const COLS = Math.floor((GAME_WIDTH - BRICK_GAP) / (BRICK_WIDTH + BRICK_GAP))
    
    for (let row = 0; row < ROWS; row++) {
      for (let col = 0; col < COLS; col++) {
        bricks.push({
          x: col * (BRICK_WIDTH + BRICK_GAP) + BRICK_GAP,
          y: row * (BRICK_HEIGHT + BRICK_GAP) + BRICK_GAP + 30,
          visible: true
        })
      }
    }
    return bricks
  })

  const resetGame = () => {
    ballRef.current = {
      x: GAME_WIDTH / 2,
      y: GAME_HEIGHT - 30,
      dx: 3,
      dy: -3
    }
    paddleRef.current = {
      x: GAME_WIDTH / 2 - PADDLE_WIDTH / 2
    }
    setGameStarted(false)
    setBricks(prev => prev.map(brick => ({ ...brick, visible: true })))
    setScore(0)
  }

  const startGame = () => {
    if (!gameStarted) {
      setGameStarted(true)
      // Reset ball position when starting
      ballRef.current = {
        x: GAME_WIDTH / 2,
        y: GAME_HEIGHT - 30,
        dx: 3,
        dy: -3
      }
    }
  }

  useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas) return

    const ctx = canvas.getContext('2d')
    if (!ctx) return

    let animationFrameId: number

    const draw = () => {
      // Clear canvas and set white background
      ctx.fillStyle = '#fff'
      ctx.fillRect(0, 0, GAME_WIDTH, GAME_HEIGHT)

      // Draw paddle
      ctx.fillStyle = '#000'
      ctx.fillRect(paddleRef.current.x, GAME_HEIGHT - PADDLE_HEIGHT - 10, PADDLE_WIDTH, PADDLE_HEIGHT)
      // Add pixel art detail to paddle
      ctx.fillStyle = '#fff'
      ctx.fillRect(paddleRef.current.x + 2, GAME_HEIGHT - PADDLE_HEIGHT - 8, 4, 4)

      // Draw ball
      ctx.beginPath()
      ctx.arc(ballRef.current.x, ballRef.current.y, BALL_SIZE, 0, Math.PI * 2)
      ctx.fillStyle = '#000'
      ctx.fill()
      ctx.closePath()

      // Draw bricks
      bricks.forEach((brick, index) => {
        if (brick.visible) {
          ctx.fillStyle = '#000'
          ctx.fillRect(brick.x, brick.y, BRICK_WIDTH, BRICK_HEIGHT)
          // Add pixel art details
          ctx.fillStyle = '#fff'
          ctx.fillRect(brick.x + 2, brick.y + 2, 4, 4)
          ctx.fillRect(brick.x + BRICK_WIDTH - 6, brick.y + 2, 4, 4)
        }
      })

      // Draw border
      ctx.strokeStyle = '#000'
      ctx.lineWidth = 2
      ctx.strokeRect(0, 0, GAME_WIDTH, GAME_HEIGHT)
    }

    const update = () => {
      if (!gameStarted) return

      const ball = ballRef.current
      const paddle = paddleRef.current

      // Move ball
      ball.x += ball.dx
      ball.y += ball.dy

      // Wall collision
      if (ball.x <= BALL_SIZE || ball.x >= GAME_WIDTH - BALL_SIZE) {
        ball.dx *= -1
      }
      if (ball.y <= BALL_SIZE) {
        ball.dy *= -1
      }

      // Paddle collision
      if (
        ball.y >= GAME_HEIGHT - PADDLE_HEIGHT - BALL_SIZE - 10 &&
        ball.x >= paddle.x &&
        ball.x <= paddle.x + PADDLE_WIDTH
      ) {
        ball.dy = -Math.abs(ball.dy)
        // Add angle based on where the ball hits the paddle
        const hitPosition = (ball.x - paddle.x) / PADDLE_WIDTH
        ball.dx = 6 * (hitPosition - 0.5) // -3 to 3 based on hit position
      }

      // Brick collision
      bricks.forEach((brick, index) => {
        if (
          brick.visible &&
          ball.x >= brick.x &&
          ball.x <= brick.x + BRICK_WIDTH &&
          ball.y >= brick.y &&
          ball.y <= brick.y + BRICK_HEIGHT
        ) {
          ball.dy *= -1
          setBricks(prev => 
            prev.map((b, i) => 
              i === index ? { ...b, visible: false } : b
            )
          )
          setScore(prev => prev + 10)
        }
      })

      // Game over
      if (ball.y >= GAME_HEIGHT) {
        resetGame()
      }

      // Win condition
      if (bricks.every(brick => !brick.visible)) {
        setScore(prev => prev + 100) // Bonus points for clearing all bricks
        resetGame()
      }
    }

    const gameLoop = () => {
      update()
      draw()
      animationFrameId = requestAnimationFrame(gameLoop)
    }

    gameLoop()

    const handleMouseMove = (e: MouseEvent) => {
      const rect = canvas.getBoundingClientRect()
      const scaleX = canvas.width / rect.width
      const x = (e.clientX - rect.left) * scaleX
      paddleRef.current.x = Math.max(0, Math.min(x - PADDLE_WIDTH / 2, GAME_WIDTH - PADDLE_WIDTH))
    }

    canvas.addEventListener('mousemove', handleMouseMove)
    
    return () => {
      cancelAnimationFrame(animationFrameId)
      canvas.removeEventListener('mousemove', handleMouseMove)
    }
  }, [gameStarted, bricks])

  const handleClick = (e: React.MouseEvent) => {
    e.preventDefault()
    e.stopPropagation()
    if (!gameStarted) {
      startGame()
    }
  }

  return (
    <div className="flex flex-col items-center gap-4 p-4 bg-gray-100 min-h-screen">
      <h1 className="text-2xl mb-4" style={{ fontFamily: "'Press Start 2P', cursive" }}>Arkanoid</h1>
      <div className="text-lg mb-2" style={{ fontFamily: "'Press Start 2P', cursive" }}>Score: {score}</div>
      <div className="relative shadow-lg">
        <canvas
          ref={canvasRef}
          width={GAME_WIDTH}
          height={GAME_HEIGHT}
          className="border-2 border-black cursor-none bg-white"
        />
        {!gameStarted && (
          <div 
            className="absolute inset-0 flex flex-col items-center justify-center bg-white bg-opacity-80"
            onClick={handleClick}
          >
            <p className="text-lg mb-4" style={{ fontFamily: "'Press Start 2P', cursive" }}>
              Click to Start
            </p>
            <p className="text-sm text-center max-w-[300px]" style={{ fontFamily: "'Press Start 2P', cursive" }}>
              Move mouse to control paddle
            </p>
          </div>
        )}
      </div>
    </div>
  )
}
